<script>
import apiUtils from './ApiUtil.vue';
const DocUtil = {
    install: function (app) {
        app.config.globalProperties.$setMetaData = function (title) {

            apiUtils.getSeo().then(res => {
                //
                fuck(title, res);
            }).catch(err => {
                console.error(err)
            });



            function fuck(title, seo) {
                document.title = title + "-" + seo.title;
                // 
                const metaKeywords = document.querySelector('meta[name="keywords"]');
                // 
                metaKeywords.setAttribute("content", seo.keywords);
                // 
                const metaDesc = document.querySelector('meta[name="description"]');
                // 
                metaDesc.setAttribute("content", seo.desc);
            }
        }
    }


}

export default DocUtil;
</script>
