<script>
import apiUtils from './ApiUtil.vue';
//{'maxPage':1,'data':[ "hisId":132, "id": 1,  "title": "1",
// "artist_name": "1",
// "lated_seqno": 10,
// "seq": 5,
// "cover": "static/picture/011.jpg"]}
const cacheKey = `history`;

const historyUtil = {

    // 
    syncHistory: function () {
        const history = apiUtils.getHistory();
        localStorage.setItem(cacheKey, JSON.stringify(history));
    },
    //
    pushHistory: function () {
        var hist = this.getHistory();
        return apiUtils.pushHistory(hist);
    },
    // 
    addHistory: function (history) {
        console.log("add hist")
        var fuck = this.updateHistory(history)
        if (fuck == null) {
            var hist = this.getHistory();
            history.hisId = history.id;
            hist.data.push(history);
            hist.version++;
            localStorage.setItem(cacheKey, JSON.stringify(hist));
            fuck = hist;
        }
        return fuck;
    },
    //
    getHistory: function () {
        const cachedData = localStorage.getItem(cacheKey);
        if (cachedData) {
            var json = JSON.parse(cachedData)
            //
            if (json.maxPage) {
                return json;
            }
        }
        return { 'maxPage': 1, 'data': [], 'version': 1 };
    },

    //id
    getHistoryByBookId: function (bookId) {
        var hist = this.getHistory();
        for (let i = 0; i < hist.data.length; i++) {
            const element = hist.data[i];
            if (element.id == bookId) {
                return element;
            }
        }
        return null;
    },


    // 
    updateHistory: function (history) {
        var hist = this.getHistory();
        for (let i = 0; i < hist.data.length; i++) {
            const element = hist.data[i];
            if (element.id == history.id) {
                hist.data[i] = history;
                hist.version++;
                localStorage.setItem(cacheKey, JSON.stringify(hist));
                return hist;
            }
        }
        return null;
    },
    //
    delHistory: function (history) {
        var hist = this.getHistory();
        for (let i = 0; i < hist.data.length; i++) {
            const element = hist.data[i];
            if (element.hisId == history.hisId) {
                hist.data.splice(i, 1);
                i--;
                break;
            }
        }
        hist.version++;
        localStorage.setItem(cacheKey, JSON.stringify(hist));

        if (apiUtils.checkLogin()) {
            this.pushHistory();
        }

        return hist;
    }
}
export default historyUtil;
</script>
