<!--  -->
<template>
    <li :class="['comic-pic-item', { 'pic-unloaded': !loaded }, { 'pic-loaded': loaded }]" :index="index" :seq="seq"
        :total="total">

        <div class="comic-pic-pad" :style="{ paddingTop: padTop }">
            <div class="err-info" v-if="loadError">
                <span>~</span>
                <a class="reload-btn" @click="reloadImage"></a>
            </div>
        </div>
        <div class="comic-pic-box">
            <img class="comic-pic" :key="imageKey" :src="imageUrl" @load="onImageLoad" @error="onImageError"
                :data-timeout="imageTimeout" ref="image" />
        </div>
    </li>
</template>
<script>
export default {
    props: {
        //
        index: {
            type: Number,
            required: true,
        },
        //
        seq: {
            type: String,
            required: true,
        },
        timeout: {
            type: Number,
            default: 0,
        },
        imageUrl: {
            type: String,
            required: true,
        }, scrollToMe: {
            type: Boolean,
            default: false,
        }, total: {
            type: Number,
            default: 0,
        }
    },
    data() {
        return {
            padTop: "110%",
            imageTimeout: 2000,
            loadError: false,
            loaded: false,
            imageKey: 0
        };
    },
    methods: {
        onImageLoad() {
            this.padTop = `${(this.$refs.image.clientHeight / this.$refs.image.clientWidth) * 100}%`;
            this.loaded = true;
            this.imageKey = Date.now();
            
        },
        onImageError() {
            this.loadError = true;
        },
        reloadImage() {
            this.loadError = false;
            // key1
            this.imageKey += 1;
        },
    },
};
</script>