<!--  -->
<template>

    <div v-bind:class="{ 'nav-menu-box': true, 'show': isShow }" v-bind:style="{ 'display': display }">
        <div class="nav-menu-bg" @click="setShow"></div>
        <div class="nav-menu">
            <ul class="nav-list">
                <li class="item" :data-button-id="10031">
                    <a class="link" @click="clickIndex">
                        <i class="icon index">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
                <li class="item" v-cloak :data-button-id="10006" @click="clickCategory">
                    <a class="link" href="javascript:;">
                        <i class="icon category">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
                <li class="item" :data-button-id="10007" @click="clickRank">
                    <a class="link" href="javascript:;">
                        <i class="icon rank">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
                <li class="item" :data-button-id="10003" @click="clickBookshelf">
                    <a class="link top-bar-collect" href="javascript:void(0)">
                        <i class="icon collect">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
                <li class="item" :data-button-id="10004" @click="clickHistory">
                    <a class="link" href="javascript:;">
                        <i class="icon history">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
                <li class="item" :data-button-id="10008" @click="clickSupport">
                    <a class="link support" href="javascript:void(0);">
                        <i class="icon feedback">[]</i>
                        <span class="name"></span>
                    </a>
                </li>
            </ul>
            <a class="btn-home" href="javascript:void(0)" :data-button-id="10009" @click="clickUserCenter">[]</a>
        </div>
    </div>
    <!--  -->
    <modal-confirm v-if="isModalVisible" message="" cancel-text="" ok-text=""
        @hide="onModalHide" @ok="onModalOk"></modal-confirm>
</template>
<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    props: ['show'],

    data() {
        return {

            isShow: this.show,
            display: 'none',
            isModalVisible: false
        }
    },
    methods: {
        clickIndex() {
            this.setShow();
            this.$router.push("/");
        },
        clickCategory() {
            this.setShow();
            // 
            this.$router.push('/category');

        },
        clickRank() {

            this.setShow();
            // 
            this.$router.push('/rank');
        },
        clickBookshelf() {
            // 
            if (apiUtils.checkLogin()) {
                this.setShow();
                this.$router.push('collection');
            } else {
                this.showModal();
            }

        },
        clickHistory() {
            // 
            this.setShow();
            // 
            this.$router.push('/history');
        },
        clickSupport() {
            // 
            this.setShow();
            // 
            this.$router.push('/help');
        },
        clickUserCenter() {
            // 
            this.setShow();
            this.$router.push('/userCenter')
        },
        setShow() {
            var newValue = !this.isShow;
            if (newValue) {
                this.display = 'block';
            }
            setTimeout(() => {
                this.isShow = newValue;
                if (!newValue) {
                    setTimeout(() => {
                        this.display = 'none';
                    }, 300);
                }
            }, 10)
        },
        showModal() {
            this.isModalVisible = true;
        },
        onModalHide() {
            console.log("");
            this.isModalVisible = false;
        },
        onModalOk() {
            console.log("");
            this.isModalVisible = false;
            this.$router.push("/login")
        }

    }, watch: {
        //
        show(newValue) {

            if (newValue) {
                this.display = 'block';
            }
            setTimeout(() => {
                this.isShow = newValue;
                if (!newValue) {
                    setTimeout(() => {
                        this.display = 'none';
                    }, 300);
                }
            }, 100)

        }
    }
};


</script>