<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {

    data() {
        return {
            originFontSize: document.documentElement.style.fontSize,
            showPay: false,
            mount: 0,
            price: 0,
            account: "",
            isModalVisible: false,

        }
    },

    methods: {
        back() {
            this.$router.back();
        },
        pay(price) {

            // 
            apiUtils.checkOrder().then(data => {
                //
                if (data.hasNoPayOder == 1) {
                    this.showModal();
                } else {
                    this.showPayType(price);
                }
            })
        },
        showPayType(price){
            apiUtils.getOrderInfo().then(data=>{
                if(data.price == 0){
                    this.showPay = true;
                    this.price = price;
                    this.mount = price * 100;
                   
                }else{
                    this.showPay  =true;
                    this.price = data.price;
                    this.mount = data.mount;
                }
                this.account = data.account;
            })

        }
        ,
        closePay() {
            this.showPay = false;
        },
        payment(type) {
            console.log("", type)

            this.closePay();
           
            // window.location.href="https://www.baidu.com"
        },
        showModal() {
            this.reset();
            this.isModalVisible = true;
        },
        onModalHide() {
            console.log("");
            this.set();
            this.isModalVisible = false;
        },
        onModalOk() {
            this.showPayType(0);
        },
        set() {
            document.documentElement.style.fontSize = "13.33333333vw";
        },
        reset() {
            if (!this.originFontSize) {
                this.originFontSize = '20px';
            }
            document.documentElement.style.fontSize = this.originFontSize;
        }
    },
    mounted() {

        this.set();
        this.$setMetaData("");
        // 
        apiUtils.checkOrder().then(data => {
            //
            if (data.hasNoPayOder == 1) {
                this.showModal();
            }
        })

    },
    unmounted() {
        this.reset();
    }
};
</script>

