<script>
import axios from 'axios';
import historyUtil from './HistoryUtil.vue';
const baseUrl = ""
//  axios  
const api = axios.create({
    baseURL: baseUrl,
    timeout: 10000,
    withCredentials: true, //  Cookie
});

//  axios  
const noAuthApi = axios.create({
    baseURL: baseUrl,
    timeout: 10000,
    withCredentials: true, //  Cookie
});

// 
api.interceptors.request.use((config) => {
    //  cookie  localStorage 
    const isLoggedIn = checkUserLoginStatus(); // 
    if (isLoggedIn) {
        //  token 
        const token = getToken(); //  token
        config.headers.Authorization = `Bearer ${token}`;
    } else {
        // 
        redirectToLoginPage(); // 
    }
    return config;
});

api.interceptors.response.use(
    response => {
        // 
        const data = response.data;
        if (data && data.code === 200) {
            return data.data;
        } else if (data.code == 404) {
            console.log(baseUrl + response.config.url)
            const url = new URL(baseUrl + "/" + response.config.url);
            const bookId = url.searchParams.get('bookId');
            const seq = url.searchParams.get('seq');
            return window.location.href = '/#/err/' + bookId + '/' + seq;// 
        } else {
            console.error('Response error:', data);
            return Promise.reject(new Error(data.message));
        }
    },
    error => {
        // 
        console.error('Response error:', error);
        return Promise.reject(error);
    }
);


noAuthApi.interceptors.response.use(
    response => {
        // 
        const data = response.data;
        if (data && data.code === 200) {
            return data.data;
        } else if (data.code == 404) {
            const url = new URL(baseUrl + "/" + response.config.url);
            const bookId = url.searchParams.get('bookId');
            const seq = url.searchParams.get('seq');
            return window.location.href = '/#/err/' + bookId + '/' + seq;// 
        } else {
            console.error('Response error:', data);
            return Promise.reject(new Error(data.message));
        }
    },
    error => {
        // 
        console.error('Response error:', error);
        return Promise.reject(error);
    }
);


function checkUserLoginStatus() {
    const isLoggedIn = window.localStorage.getItem('isLoggedIn'); //  localStorage 

    return isLoggedIn === 'true';
}



function getToken() {
    const token = localStorage.getItem('token'); //  token
    if (token) {
        return token; //  token
    } else {
        return null; //  token null
    }
}

function redirectToLoginPage() {
    window.location.href = '/#/login' // 
}




//api()
function noCacheApi(axObj, api, options) {
    return axObj.get(api, options).then(response => {
        return Promise.resolve(response);
    });
}

// api
function cacheApi(axObj, api, options, cacheTime = 1000) {
    //const cacheTime = 1000 * 60 * 24;//

    const cacheKey = `${api}_${JSON.stringify(options)}`;
    const cachedData = localStorage.getItem(cacheKey);
    if (cachedData) {

        const { createTime, response } = JSON.parse(cachedData);
        const now = new Date().getTime();
        if (now - createTime <= cacheTime) {
            console.log("find cache", api)
            return Promise.resolve(response);
        }
    }
    return axObj.get(api, options).then(response => {
        console.log("insert cache", api)
        const createTime = new Date().getTime();
        const cacheData = JSON.stringify({ createTime, response });
        localStorage.setItem(cacheKey, cacheData);
        return Promise.resolve(response);
    });
}


